/* Regular Theme */
.sce-edit-comment {
	margin: 20px 0;
	&.regular {
		.sce-comment-edit-buttons {
			margin: 10px 0;
			button {
				display: inline-block;
				border-radius: 2px;
				padding: 10px 20px;
				margin-right: 10px;
			}
			button:last-of-type {
				margin-right: 0;
			}
			.sce-comment-save {
				background-color: #4CAF50;
				color: #FFF;
				&:hover {
					background-color: darken( #4CAF50, 10% );
				}
			}
			.sce-comment-cancel {
				background-color: #e7e7e7;
				color: #000;
				&:hover {
					background-color: darken( #e7e7e7, 10% );
				}
			}
			.sce-comment-delete {
				background-color: #f44336;
				color: #FFF;
				&:hover {
					background-color: darken( #f44336, 10% );
				}
			}
			.sce-comment-delete {
				float: right;
			}
			.sce-timer {
				margin-top: 10px;
			}
		}
	}
	&.dark {
		.sce-comment-edit-buttons {
			margin: 10px 0;
			button {
				display: inline-block;
				background-color: #333;
				color: #FFF;
				margin-right: 10px;
				border-radius: 2px;
				padding: 10px 20px;
				&:hover {
					background-color: darken( #333, 10% );
				}
			}
			button:last-of-type {
				margin-right: 0;
			}
			.sce-comment-delete {
				float: right;
			}
			.sce-timer {
				margin-top: 10px;
			}
		}
	}
	&.light {
		.sce-comment-edit-buttons {
			margin: 10px 0;
			button {
				display: inline-block;
				background-color: #EEE;
				color: #000;
				margin-right: 10px;
				border-radius: 2px;
				padding: 10px 20px;
				&:hover {
					background-color: darken( #EEE, 10% );
				}
			}
			button:last-of-type {
				margin-right: 0;
			}
			.sce-comment-delete {
				float: right;
			}
			.sce-timer {
				margin-top: 10px;
			}
		}
	}
}